import {Component, Input} from "@angular/core";
import {IoDiagnosticsInstanceService} from "./io-diagnostics-instance.service";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from '@util/icon';
import {VirtualDiskIdentifier} from "./model/virtual-disk-identifier";

@Component({
   templateUrl: "io-diagnostics-instance-stats-selector.html",
   styleUrls: ["io-diagnostics-instance-stats-selector.scss"],
   selector: "vsan-io-diagnostics-instance-stats-selector",
})
export class IoDiagnosticsInstanceStatsSelectorComponent {
   Icon: typeof Icon = Icon;

   private isAfterViewInit: boolean = false;

   private _vmdksIdentifier: VirtualDiskIdentifier[];

   @Input()
   set vmdksIdentifier(value: VirtualDiskIdentifier[]) {
      this._vmdksIdentifier = value;
      this._vmdksIdentifier.sort((id1, id2) => id1.name?.localeCompare(id2.name));
      this.preselectVmdk();
   }
   get vmdksIdentifier(): VirtualDiskIdentifier[] {
      return this._vmdksIdentifier;
   }

   selectedVmdkIdentifier: VirtualDiskIdentifier;

   constructor(private ioDiagnosticsInstanceService: IoDiagnosticsInstanceService) {
   }

   ngAfterViewInit(): void {
      this.isAfterViewInit = true;
      this.preselectVmdk();
   }

   preselectVmdk(): void {
      // Preselect first item after view init
      if (this.isAfterViewInit && !VsanUiUtils.isEmpty(this.vmdksIdentifier)) {
         this.selectedVmdkIdentifier = this._vmdksIdentifier[0];
         this.selectionChanged(this.selectedVmdkIdentifier);
      } else {
         this.selectedVmdkIdentifier = null;
      }
   }

   selectionChanged(vmdkIdentifier: VirtualDiskIdentifier): void {
      if (vmdkIdentifier) {
         this.ioDiagnosticsInstanceService.selectVmdkId(vmdkIdentifier.id);
      }
   }
}