import {AfterViewInit, Component, ElementRef, Input, OnDestroy, ViewChild} from "@angular/core";
import {GraphViewService} from "./graph-view.service";
import {GraphElements} from "@generated/graph-elements";
import {ReferenceWatcher} from "@util/reference-watcher";
import {ModalBuilder} from "@util/modal-builder";
import {IoVmMetricsDialogContext} from "../dialog/io-vm-metrics-dialog.component";
import {GraphNodeDetails, GraphNodeType} from "./model/graph-node-details";
import {ManagedObject} from "@service/managed-object";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {GraphThemeService} from "./graph-theme.service";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanRoutes} from "../../../../vsan-routes";

@Component({
   templateUrl: "graph-view.html",
   styleUrls: ["graph-view.scss"],
   selector: "vsan-graph-view",
   providers: [ReferenceWatcher],
})
export class GraphView implements AfterViewInit, OnDestroy {

   isAfterViewInit: boolean = false;

   @ViewChild("graphContainer")
   graphContainer: ElementRef;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   startTime: number;

   @Input()
   endTime: number;

   _graphElements: GraphElements;
   @Input()
   set graphElements(value: GraphElements) {
      this._graphElements = value;
      if (this.isAfterViewInit) {
         this.initGraph();
      }
   }
   get graphElements(): GraphElements {
      return this._graphElements;
   }

   constructor(private graphService: GraphViewService,
               private refWatcher: ReferenceWatcher,
               private graphThemeService: GraphThemeService) {
      this.refWatcher.watchSubscriptionRef(
            this.graphThemeService.onThemeChanged.subscribe(theme => this.initGraph()));
   }

   ngAfterViewInit(): void {
      this.isAfterViewInit = true;
      this.initGraph();

      this.refWatcher.watchSubscriptionRef(this.graphService.onGraphNodeClicked
            .subscribe((data) => this.openDetails(data)));
   }

   private openDetails(data: GraphNodeDetails): void {
      switch (data.type) {
         case GraphNodeType.DISK_GROUP:
         case GraphNodeType.DISK:
         case GraphNodeType.NIC:
            this.openPerformanceDialog(data.entityName, data.hostName, data.hostUuid, data.type);
            break;
      }
   }

   private openPerformanceDialog(entity: string, hostName: string, hostUuid: string, objectType: GraphNodeType): void {
      const context: IoVmMetricsDialogContext = {
         clusterRef: this.clusterRef,
         entityName: entity,
         hostName: hostName,
         hostUuid: hostUuid,
         isHistorical: VsanUiUtils.isEmpty(hostName),
         type: objectType,
         startDate: this.startTime,
         endDate: this.endTime,
      };
      new ModalBuilder(VsanRoutes.VM_IO_DIAGNOSTICS_PERF_METRICS)
            .setSize(VsanDialogUtil.VM_IO_DIAGNOSTICS_METRICS_DIALOG.width,
                     VsanDialogUtil.VM_IO_DIAGNOSTICS_METRICS_DIALOG.height)
            .open(context);
   }

   private initGraph(): void {
      if (this.graphElements) {
         this.graphService.initCytoscape(this.graphContainer, this.graphElements, this.clusterRef);
      }
   }

   ngOnDestroy(): void {
      this.graphService.destroyCytoscape();
   }
}
