import {Component} from "@angular/core";
import {VmIoDiagnosticsStats} from "@generated/vm-io-diagnostics-stats";
import {IoDiagnosticsService} from "@generated/io-diagnostics-service";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {Loader} from "@service/flow/loader.decorator";
import {ReferenceWatcher} from "@util/reference-watcher";
import {IoDiagnosticsInstanceService} from "./io-diagnostics-instance.service";
import {ManagedObject} from "@service/managed-object";
import {VsanIODiagnosticsInstanceResult} from "@generated/vsan-i-o-diagnostics-instance-result";
import {DurationPickerState} from "@component/datetime-picker/time-last-field-picker/last-field-date-picker.component";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {VsanIODiagnosticsInstanceEventType} from "@generated/vsan-i-o-diagnostics-instance-event-type";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {TaskMonitorService} from "@service/task-monitor-service";
import {IoDiagnosticsUtil} from "./io-diagnostics.util";
import {VsanIODiagnosticsFailedCheckType} from "@generated/vsan-i-o-diagnostics-failed-check-type";

@Component({
   templateUrl: "io-diagnostics.html",
   styleUrls: ["io-diagnostics.scss"],
   selector: "vsan-io-diagnostics",
   providers: [ReferenceWatcher],
})
export class IoDiagnosticsComponent {

   private static readonly REFRESH_INTERVAL_MS: number = 10000;
   private static readonly REFRESH_LOADING_INDICATOR: string = "refresh-loading";
   private timeoutId: number;

   @LoadingIndicator({name: IoDiagnosticsComponent.REFRESH_LOADING_INDICATOR})
   loading: boolean;

   @ErrorMessage({name: IoDiagnosticsComponent.REFRESH_LOADING_INDICATOR})
   errorMessage: string;
   unavailableViewSpec: UnavailableViewSpec;
   /**
    * True when view is on instance list level. False when view is on instance stats level.
    */
   isInstanceListView: boolean = true;

   ioInstanceResult: VsanIODiagnosticsInstanceResult;
   ioInstanceStats: VmIoDiagnosticsStats;
   ioInstanceEvents: VsanIODiagnosticsInstanceEventType[];

   timePickerState: DurationPickerState;

   constructor(private ioDiagnosticsService: IoDiagnosticsService,
               private ioDiagnosticsInstanceService: IoDiagnosticsInstanceService,
               private refWatcher: ReferenceWatcher,
               private taskService: TaskMonitorService) {
   }

   ngOnInit(): void {
      this.refWatcher.watchSubscriptionRef(this.ioDiagnosticsInstanceService.onStatsQueried.subscribe(
            instance => this.queryInstanceStats(instance)));
      this.refWatcher.watchSubscriptionRef(this.ioDiagnosticsInstanceService.onInstanceListQueried.subscribe(
            () => this.fetch()));
      this.refWatcher.watchSubscriptionRef(this.ioDiagnosticsInstanceService.onInstanceListViewEnabled.subscribe(
            () => this.enableInstanceListView()));
      this.refWatcher.watchSubscriptionRef(this.ioDiagnosticsInstanceService.onErrorThrown.subscribe(
            (errorMsg) => this.errorMessage = errorMsg));
      this.refreshLoadIndicator();
   }

   @RefreshHandler
   @Loader({name: IoDiagnosticsComponent.REFRESH_LOADING_INDICATOR, srMessage: "vsan.monitor.vm.iodiagnostics.label"})
   private async refreshLoadIndicator(): Promise<void> {
      await this.fetch();
   }

   @Loader()
   private async refresh(): Promise<void> {
      await this.fetch();
   }

   private async fetch(): Promise<void> {
      this.refWatcher.clearTimeout(this.timeoutId);
      this.ioInstanceResult = await this.ioDiagnosticsService.getIoDiagnosticsInstances(ManagedObject.contextObject);
      if (this.hasRunningInstance()) {
         // Refetch instances after some timeout in order to check for the existence of running instances
         this.timeoutId = this.refWatcher.setTimeout(() => this.refresh(), IoDiagnosticsComponent.REFRESH_INTERVAL_MS);
      }
      this.unavailableViewSpec = IoDiagnosticsUtil.createUnavailableViewSpec(this.ioInstanceResult);

      // No need to requery instance stats on refresh as it's state cannot be changed.
   }

   private hasRunningInstance(): boolean {
      return this.ioInstanceResult?.precheckResult?.failedChecks?.some(precheck =>
            precheck.unsupportedType === VsanIODiagnosticsFailedCheckType.RUNNING_INSTANCE);
   }

   @ErrorHandler()
   setErrorMessage(error: string): void {
      this.errorMessage = error;
   }

   @Loader({name: IoDiagnosticsComponent.REFRESH_LOADING_INDICATOR})
   async queryInstanceStats(instanceName: string): Promise<void> {
      this.ioInstanceStats = await this.ioDiagnosticsService.getIoDiagnosticsStats(instanceName, ManagedObject.contextObject);
      this.ioInstanceEvents = this.ioInstanceResult.instances.find(instance => instance.name === instanceName).events;
      this.isInstanceListView = false;
   }

   enableInstanceListView(): void {
      this.isInstanceListView = true;
   }

   getFailedChecksReasons(): string[] {
      return IoDiagnosticsUtil.getFailedChecksReasons(this.ioInstanceResult?.precheckResult?.failedChecks);
   }

   awaitThenReload = (taskRef: ManagedObject) => {
      this.taskService.getAwaitResult(taskRef)
            .then(() => this.fetch())
            .catch(e => this.errorMessage = e);
   };
}