import {Component, Input, OnInit} from "@angular/core";
import {VmIoDiagnosticsStats} from "@generated/vm-io-diagnostics-stats";
import {IoDiagnosticsInstanceService} from "./io-diagnostics-instance.service";
import {VirtualDiskLatency} from "./model/virtual-disk-latency";
import {VsanUiUtils} from "@util/vsan-util";
import {ReferenceWatcher} from "@util/reference-watcher";
import {GraphElements} from "@generated/graph-elements";
import {ManagedObject} from "@service/managed-object";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {VsanIODiagnosticsInstanceEventType} from "@generated/vsan-i-o-diagnostics-instance-event-type";
import {VirtualDiskIdentifier} from "./model/virtual-disk-identifier";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {IoDiagnosticsUtil} from "./io-diagnostics.util";

@Component({
   templateUrl: "io-diagnostics-instance-stats.html",
   styleUrls: ["io-diagnostics-instance-stats.scss"],
   selector: "vsan-io-diagnostics-instance-stats",
   providers: [ReferenceWatcher],
})
export class IoDiagnosticsInstanceStatsComponent implements OnInit {

   vmdksLatency: VirtualDiskLatency[];
   vmdksIdentifier: VirtualDiskIdentifier[];
   selectedGraphElements: GraphElements;
   targetRef: ManagedObject = ManagedObject.contextObject;

   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;

   private _ioInstanceStats: VmIoDiagnosticsStats;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   get ioInstanceStats(): VmIoDiagnosticsStats {
      return this._ioInstanceStats;
   }

   set ioInstanceStats(value: VmIoDiagnosticsStats) {
      this._ioInstanceStats = value;
      if (value && !VsanUiUtils.isEmpty(value.graphElements)) {
         this.vmdksLatency = VirtualDiskLatency.fromGraphElementsList(value.graphElements);
         this.vmdksIdentifier = VirtualDiskIdentifier.fromGraphElementsList(value.graphElements);
      }
   }

   @Input()
   ioInstanceEvents: VsanIODiagnosticsInstanceEventType[];

   @Input()
   failedChecksReasons: string[];

   constructor(private ioDiagnosticsInstanceService: IoDiagnosticsInstanceService,
         private navigationService: NavigationService,
         private refWatcher: ReferenceWatcher) {
   }

   ngOnInit(): void {
      this.refWatcher.watchSubscriptionRef(this.ioDiagnosticsInstanceService.onVmdkIdSelected.subscribe(
            vmdkId => this.selectVmdkLatency(vmdkId)));
   }

   enableInstanceListView(): void {
      this.ioDiagnosticsInstanceService.enableInstanceListView();
   }

   navigateToGeneralVmStats(): void {
      this.navigationService.navigateTo(PlatformExtension.VM_PERF_OVERVIEW, this.targetRef);
   }

   private selectVmdkLatency(vmdkId: string): void {
      if (!this.ioInstanceStats && VsanUiUtils.isEmpty(this.ioInstanceStats.graphElements)) {
         return;
      }
      this.selectedGraphElements = this.ioInstanceStats.graphElements.find(graphEl => graphEl.id === vmdkId);
   }

   get startDate(): string {
      return VsanDateTimeUtils.formatDate(this.ioInstanceStats.timeRange.startTime);
   }

   get endDate(): string {
      return VsanDateTimeUtils.formatDate(this.ioInstanceStats.timeRange.endTime);
   }

   showGraph(): boolean {
      return IoDiagnosticsUtil.canRenderGraph(this.selectedGraphElements);
   }

   /**
    * Display different messages based on nodes/edges existence:
    * 1) If both are missing no stats are collected.
    * 2) If either is missing backend doesn't return proper information to create graph (usually not expected)
    */
   noGraphMessage(): string {
      const localizationKey: string = IoDiagnosticsUtil.hasNoStats(this.selectedGraphElements)
            ? "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.noStats"
            : "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.insufficientStats";
      return VsanUiUtils.getString(localizationKey);
   }
}