import {ExportDataExtractor} from "@component/export/export-data-extractor";
import {StringUtil} from "@util/string-util";
import {IoDiagnosticsUtil} from "../io-diagnostics.util";
import {VirtualDiskLatency} from "../model/virtual-disk-latency";

export class LatencyExportConfig {

   static readonly vmdkLatencyFileName: string = "vmdks_latencies";

   static getLatencyDataExtractors(): ExportDataExtractor[] {
      return [
         LatencyExportConfig.vmdkName,
         LatencyExportConfig.latencyType,
         LatencyExportConfig.domComponentUuid,
         LatencyExportConfig.readLatencyAvg,
         LatencyExportConfig.readLatencyStddev,
         LatencyExportConfig.writeLatencyAvg,
         LatencyExportConfig.writeLatencyStddev,
      ];
   }

   private static vmdkName: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.objectName",
      value: (vmdksLatency: VirtualDiskLatency) => IoDiagnosticsUtil.getVmdkName(vmdksLatency.name),
   };

   private static latencyType: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.latencyType",
      value: (vmdksLatency: VirtualDiskLatency) => StringUtil.sentenceCase(vmdksLatency.typeLabel),
   };

   private static domComponentUuid: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.domComponentUuid",
      value: (vmdksLatency: VirtualDiskLatency) => vmdksLatency.domComponentUuid,
   };

   private static readLatencyAvg: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.readLatencyAvg",
      value: (vmdksLatency: VirtualDiskLatency) => IoDiagnosticsUtil.formatLatency(vmdksLatency.avgRead),
   };

   private static readLatencyStddev: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.readLatencyStddev",
      value: (vmdksLatency: VirtualDiskLatency) => IoDiagnosticsUtil.formatLatency(vmdksLatency.stddevRead),
   };

   private static writeLatencyAvg: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.writeLatencyAvg",
      value: (vmdksLatency: VirtualDiskLatency) => IoDiagnosticsUtil.formatLatency(vmdksLatency.avgWrite),
   };

   private static writeLatencyStddev: ExportDataExtractor = {
      label: "vsan.monitor.vm.iodiagnostics.grid.column.writeLatencyStddev",
      value: (vmdksLatency: VirtualDiskLatency) => IoDiagnosticsUtil.formatLatency(vmdksLatency.stddevWrite),
   };
}