import {Injectable} from "@angular/core";
import {Observable, Subject} from "rxjs";
import {ThemeListenerService} from "@service/theme-listener.service";
import {ReferenceWatcher} from "@util/reference-watcher";
import {SvgUtil} from "./cytoscape-properties/svg-util";
import {GraphConstants} from "./cytoscape-properties/graph-constants";
import {ParentNodeImageColors} from "./model/parent-node-image-colors";
import {Logger} from "@util/logger";
import {GraphElementStyles} from "./model/graph-element-styles";

@Injectable()
export class GraphThemeService {

   static readonly IO_DIAGNOSTICS_GRAPH: string = "ioDiagnostics";

   /**
    * The theme received from SDK's theme themeChanged event.
    */
   private currentTheme: string;

   /**
    * The Subject that allows others to subscribe notice when theme is changed
    */
   private themeChanged: Subject<string> = new Subject<string>();

   /**
    * The Observable exposed to the public.
    */
   onThemeChanged: Observable<string> = this.themeChanged.asObservable();

   constructor(private themeListenerService: ThemeListenerService, private refWatcher: ReferenceWatcher) {
      this.refWatcher.watchSubscriptionRef(
         this.themeListenerService.whenThemeChanged.subscribe((theme) => this.loadTheme(theme)));
      this.loadTheme(this.themeListenerService.getCurrentTheme());
   }

   private loadTheme(theme: string): void {
      this.currentTheme = theme;
      this.themeChanged.next(this.currentTheme);
   }

   /**
    * Get the current theme name from theme service, apply color palette.
    * Then return Cytoscape graph theme object for the specific graph type.
    * @param {string} graph type.
    * @returns Graph theme object or empty object if the color palette does not exist.
    */
   getThemeStyles(graphType: string): GraphElementStyles {
      const colorPalette: GraphColorPalette | null = GraphColorPalettes.get(this.currentTheme);
      if (colorPalette === null) {
         Logger.error("Cannot find graph color palette");
         return {};
      }
      switch (graphType) {
         case GraphThemeService.IO_DIAGNOSTICS_GRAPH:
            return this.getIoDiagnosticsGraphStyles(colorPalette);
         default:
            Logger.error("Cannot find graph type styles");
            return {};
      }
   }

   private getIoDiagnosticsGraphStyles(colorPalette: GraphColorPalette): GraphElementStyles {
      const parentNodeImageColors: ParentNodeImageColors = new ParentNodeImageColors(colorPalette.badgeColor,
            colorPalette.badgeBorder,
            colorPalette.badgeBackground,
            colorPalette.color);
      const theme: GraphElementStyles  = {
         node: {
            "background-color": colorPalette.background,
            "border-color": colorPalette.link,
            "color": colorPalette.link,
         },
         nodeHover: {
            "background-color": colorPalette.nodeHoverBackground,
            "color": colorPalette.nodeHoverColor,
         },
         nodeRoot: {
            "color": colorPalette.color,
            "border-color": colorPalette.border,
         },
         compoundNode: {
            // Host icon and host name are added as SVG background images
            "background-image": (compoundNode) => SvgUtil.getParentNodeImages(compoundNode,
                  GraphConstants.ICON_SIZE,
                  parentNodeImageColors),
            "border-color": colorPalette.border,
         },
         edge: {
            "line-color": colorPalette.edgeColor,
            "target-arrow-color": colorPalette.edgeColor,
         },
         edgeDanger: {
            "line-color": colorPalette.danger,
            "target-arrow-color": colorPalette.danger,
         },
         edgeDummy: {
            "line-color": colorPalette.dummy,
            "target-arrow-color": colorPalette.dummy,
         },
      };
      return theme;
   }
}

interface GraphColorPalette {
   background: string;
   link: string;
   border: string;
   color: string;
   nodeHoverColor: string;
   nodeHoverBackground: string;
   edgeColor: string;
   badgeColor: string;
   badgeBorder: string;
   badgeBackground: string;
   danger: string;
   dummy: string;
}

/**
 *
 * Define the Graph color palette of themes
 *
 * @type [themeName => GraphColorPalette]
 */
class GraphColorPalettes {
   /**
    * Gets the graph color palette by theme name
    * @param {string} name of the theme
    * @returns {GraphColorPalette} the color palette or {null} if the theme is not found.
    */
   static get(name: string): GraphColorPalette | null {
      if (!GraphColorPalettes.hasOwnProperty(name)) {
         return null;
      }
      return GraphColorPalettes[name] as GraphColorPalette;
   }

   static light: GraphColorPalette = {
      background: "#FFF",
      link: "#0079b8",
      border: "#ccc",
      color: "#565656",
      nodeHoverColor: "#004d8a",
      nodeHoverBackground: "#e1f1f6",
      edgeColor: "#565656",
      badgeColor: "#00567a",
      badgeBorder: "#00567a",
      badgeBackground: "#e3f5fc",
      danger: "#E62700",
      dummy: "#ccc",
   };

   static dark: GraphColorPalette = {
      background: "#22343C",
      link: "#89cbdf",
      border: "#485764",
      color: "#adbbc4",
      nodeHoverColor: "#57c8ea",
      nodeHoverBackground: "#384850",
      edgeColor: "#adbbc4",
      badgeColor: "#ffffff",
      badgeBorder: "#0f5e80",
      badgeBackground: "#0f5e80",
      danger: "#F54F47",
      dummy: "#485764",
   };
}