import {GraphElements} from "@generated/graph-elements";

/**
 * Class is required because of https://github.com/vmware/clarity/issues/5458
 */
export class VirtualDiskIdentifier {
   constructor(
         public id: string,
         public name: string) {
   }

   static fromGraphElementsList(graphElementsList: GraphElements[]): VirtualDiskIdentifier[] {
      return graphElementsList.map(VirtualDiskIdentifier.fromGraphElements);
   }

   static fromGraphElements(graphElements: GraphElements): VirtualDiskIdentifier {
      return new VirtualDiskIdentifier(graphElements.id, graphElements.name);
   }
}