import {Injectable} from "@angular/core";
import {GraphClasses} from "@generated/graph-classes";
import {GraphEdgeData} from "@generated/graph-edge-data";
import {LatencyDetailedInfo} from "@generated/latency-detailed-info";
import {VsanIoLatencyType} from "@generated/vsan-io-latency-type";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {VsanUiUtils} from "@util/vsan-util";
import cytoscape, {EdgeSingular, SingularElementArgument} from "cytoscape";
import {VsanRoutes} from "../../../../vsan-routes";
import {LatencyDetailsModalContextData} from "../dialog/latency-details-modal-context.data";
import {IoDiagnosticsUtil} from "../io-diagnostics.util";
import {VirtualDiskLatency} from "../model/virtual-disk-latency";
import {IDestroyable} from "./model/destroyable-instance";
import {PopperInstance} from "./model/popper-instance";

/**
 * A service that renders circle shaped elements in the middle of edges (used popper.js extension).
 */
@Injectable()
export class GraphDetailsService extends PopperInstance {

   private static readonly TRIGGER_PREFIX: string = "graph-details-trigger-";
   private static readonly TRIGGER_CLASS: string = "graph-details-trigger";
   private static readonly TRIGGER_DANGER_CLASS: string = "graph-details-trigger-danger";
   private static readonly LATENCIES_FIELD_NAME: string = "latenciesStats";

   addDetails(graph: cytoscape.Core, clsRef: ManagedObject): void {
      const detailsEdges: SingularElementArgument[] = graph.edges().filter(this.hasEdgeLatency).toArray();
      for (const edge of detailsEdges) {
         this.createTriggerElement(edge, graph, clsRef);
      }
   }

   private hasEdgeLatency(edge: EdgeSingular): boolean {
      return !!edge.data(GraphDetailsService.LATENCIES_FIELD_NAME)?.length && !edge.hasClass(GraphClasses.DUMMY());
   }

   private createTriggerElement(edge: any, graph: cytoscape.Core, clsRef: ManagedObject): void {
      const popperEdge: IDestroyable = edge.popper({
         content: () => this.createPopperContentElement(edge, clsRef),
         popper: {
            removeOnDestroy: true,
         },
      });
      this.addDestroyable(popperEdge);
      const updatePopperEdge: () => void = () => (popperEdge as any).update();
      // Add handlers to reposition the popper element when the edge is reposition or the graph is panned
      edge.connectedNodes().on("position", updatePopperEdge);
      graph.on("pan", updatePopperEdge);
   }

   private createPopperContentElement(edge: SingularElementArgument, clsRef: ManagedObject): HTMLElement {
      const el: HTMLElement = IoDiagnosticsUtil.createDivElement();
      el.classList.add(GraphDetailsService.TRIGGER_CLASS);
      if (edge.hasClass(GraphClasses.DANGER())) {
         el.classList.add(GraphDetailsService.TRIGGER_DANGER_CLASS);
         el.innerText = "!";
      }
      const edgeData: GraphEdgeData = edge.data();
      el.id = this.getEdgeId(edgeData);
      el.tabIndex = 0;
      el.title = this.getEdgeElementTitle(edgeData);
      el.setAttribute("role", "button");
      this.attachDialogOpenEvent(el, edgeData, clsRef);
      document.body.appendChild(el);
      return el;
   }

   private getEdgeId(edgeData: GraphEdgeData): string {
      return GraphDetailsService.TRIGGER_PREFIX + edgeData?.id;
   }

   private getEdgeElementTitle(edgeData: GraphEdgeData): string {
      const latencyType: VsanIoLatencyType = edgeData?.latenciesStats[0]?.latencyType;
      const latencyLabel: string = edgeData?.latenciesStats[0]?.latencyLabel;
      const detailedInfo: LatencyDetailedInfo = edgeData?.latenciesStats[0]?.detailedInfo;
      const hostName: string = IoDiagnosticsUtil.getHostName(detailedInfo);
      switch (latencyType) {
         case VsanIoLatencyType.DOM_OWNER:
         case VsanIoLatencyType.PERF_DOM_OWNER:
         case VsanIoLatencyType.CAPACITY_DOM_OWNER:
            return VsanUiUtils.getString(
                  "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.domOwnerLatencyPoint.title",
                  latencyLabel);
         case VsanIoLatencyType.NETWORK:
         case VsanIoLatencyType.PERF_NETWORK:
         case VsanIoLatencyType.CAPACITY_NETWORK:
            return VsanUiUtils.getString(
                  "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.domNetworkLatencyPoint.title",
                  latencyLabel,
                  hostName);
         case VsanIoLatencyType.LSOM:
         case VsanIoLatencyType.PERF_LSOM:
         case VsanIoLatencyType.CAPACITY_LSOM:
            return VsanUiUtils.getString(
                  "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.domLsomLatencyPoint.title",
                  latencyLabel,
                  detailedInfo?.diskGroupUuid,
                  hostName);
         case VsanIoLatencyType.UNKNOWN:
         default:
            return "";
      }
   }

   private attachDialogOpenEvent(el: HTMLElement, edgeData: GraphEdgeData, clsRef: ManagedObject): void {
      const latenciesStats: VirtualDiskLatency[] = VirtualDiskLatency.fromVsanIoLatencies(edgeData?.latenciesStats);
      const dialogSize: [number, number] = this.getDialogSize(latenciesStats);

      const context: LatencyDetailsModalContextData = {
         latenciesStats: latenciesStats,
         clsRef: clsRef,
      };
      IoDiagnosticsUtil.attachUserEvents(el, () =>
            new ModalBuilder(`${VsanRoutes.VM_IO_DIAGNOSTICS_DIALOG}/latencyDetails`)
                  .setDimensions(dialogSize)
                  .open(context));
   }

   private getDialogSize(latenciesStats: VirtualDiskLatency[]): [number, number] {
      const isDomOwner: boolean = IoDiagnosticsUtil.isDomOwner(latenciesStats?.[0].type);
      return isDomOwner
            ? [
               VsanDialogUtil.VM_IO_DIAGNOSTICS_DOM_OWNER_LATENCY_DIALOG.width,
               VsanDialogUtil.VM_IO_DIAGNOSTICS_DOM_OWNER_LATENCY_DIALOG.height,
            ]
            : [
               VsanDialogUtil.VM_IO_DIAGNOSTICS_LATENCY_DIALOG.width,
               VsanDialogUtil.VM_IO_DIAGNOSTICS_LATENCY_DIALOG.height,
            ];
   }
}