import {Component, Input} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {ModalBuilder} from "@util/modal-builder";
import {IoDiagnosticsInstanceService} from "../io-diagnostics-instance.service";
import {RunInstanceModalContextData} from "../dialog/run-instance-modal-context.data";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {VsanRoutes} from "../../../../vsan-routes";

@Component({
   templateUrl: "run-instance-action.html",
   styleUrls: ["run-instance-action.scss"],
   selector: "vsan-run-instance-action",
})
export class RunInstanceActionComponent {

   @Input()
   alertActionLabel: string;

   @Input()
   failedChecksReasons: string[];

   @Input()
   clsRef: ManagedObject;

   @Input()
   tooltipPosition: string = "bottom-left";

   actionLink: AlertActionLink = {
      label: this.getRunInstanceButtonText(),
      action: () => this.runInstance(),
   };

   constructor(private ioDiagnosticsInstanceService: IoDiagnosticsInstanceService) {
   }

   isRunInstanceButtonEnabled(): boolean {
      return !this.getUnavailableMessage();
   }

   private getUnavailableMessage(): string {
      return this.failedChecksReasons && this.failedChecksReasons[0];
   }

   getRunInstanceButtonTitle(): string {
      return this.getUnavailableMessage() ||
            VsanUiUtils.getString("vsan.monitor.vm.iodiagnostics.action.runInstance.description");
   }

   getRunInstanceButtonText(): string {
      return VsanUiUtils.getString("vsan.monitor.vm.iodiagnostics.action.runInstance.label");
   }

   runInstance(): void {
      const context: RunInstanceModalContextData = {
         clsRef: this.clsRef,
         vmRef: ManagedObject.contextObject,
      };
      new ModalBuilder(`${VsanRoutes.VM_IO_DIAGNOSTICS_DIALOG}/runInstance`)
            .setSize(800, 250)
            .open(context)
            // No need to wait for the task to complete, just re-query instances.
            // There is polling for the time having a running instance.
            .then(() => this.ioDiagnosticsInstanceService.queryInstanceList())
            .catch(e => this.ioDiagnosticsInstanceService.throwError(e));
   }
}